@import "../settings";
.app-Header {
  background: $black1;
  width: 60%;
  min-width: 300px;
  max-width: 650px;
  margin: 150px auto 30px;
  text-align: center;
  position: relative;
  padding: 20px 20px;
  border-radius: 0 20px 20px 20px;
  min-height: 258px;
  box-shadow: $shadow;

  .connect-modal {
    height: 100%;
    .errorContent {
      font-weight: bold;
    }
    .header-btn {
      position: absolute;
      left: 0;
      top: -29px;

      button {
        color: $white;
        background: $black1;
        padding: 4px 14px;
        border-radius: 10px 10px 0 0;
        transition: 0.2s;

        &:hover {
          filter: brightness(135%);
        }
      }
    }
    .login {
      width: 50%;
    }
    .signup-container,
    .login-container {
      min-height: 210px;
      @include verticalCenter;

      h1 {
        margin-bottom: 8px;
      }
      /* Formulaire d'inscription */
      .form-login {
        display: grid;

        label {
          position: absolute;
          top: 84px;
          left: 50px;
        }
        .password {
          position: absolute;
          top: 130px;
        }
        svg {
          cursor: pointer;
          position: absolute;
          right: 120px;
          top: 130px;
        }
      }
      /* Formulaire de connexion */
      .signupForm {
        display: flex;
        button[disabled="disabled"],
        button:disabled {
          cursor: none;
        }

        .labelForm {
          width: 30%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: auto;
          margin: 9px 0px 60px;

          .passwordWrapper {
            position: relative;
            .inscription_label_password {
              top: 135px;
            }
            .fa-eye {
              cursor: pointer;
              position: absolute;
              top: 12px;
            }
          }
        }
        .contentForm {
          span {
            display: block;
            width: 100%;
            font-size: 1rem;
          }
          .icon_eye {
            cursor: pointer;
            position: absolute;
            top: 50%;
          }
        }

        .email {
          position: absolute;
          left: 80px;
          top: 80px;
        }
        .password {
          position: absolute;
          left: 80px;
          top: 130px;
        }
        input {
          width: 70%;
          text-align: center;
          padding: 8px 10px;
          transition: 0.2s;
        }
      }
      ul {
        .faXmark {
          color: red;
        }
        .fa-check {
          color: green;
        }
        svg {
          transform: translateX(20px) 2s;
          padding-left: 15px;
        }
      }
    }
  }
