@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "LatoWeb", sans-serif;
  font-size: 1.05rem;
}

body {
  background: $black2;
  color: $white;
}

button,
input[type="submit"] {
  background: none;
  border: none;
  cursor: pointer;
  color: white;
  background: $color1;
  transition: 0.2s;

  &:hover {
    filter: brightness(80%);
    transform: translateX(2px);
  }
}
input[type="submit"]:disabled {
  background: rgb(95, 91, 91);
  cursor: default;
  &:hover {
    transform: none;
    filter: none;
  }
}

input,
textarea {
  border-radius: 7px;
  border: none;
  resize: none;
  margin: 5px;
  padding: 8px;
  background: $white;
}
@media screen and (max-width: 768px) {
  .app-Header {
    .connect-modal {
      .login {
        width: 80%;
      }
      .signup {
        .signupForm {
          .contentForm {
            input {
              width: 100%;
            }
          }
        }
      }
      .signup-container {
        .signupForm {
          .labelForm {
            display: none;
          }
        }
      }
      .login-container {
        .form-login {
          label {
            display: none;
          }
        }
      }
    }
  }
}
